.Form {
    width: 100%;
}

.Form__Row {
    width: 100%;
    margin: 10px auto;
}

.Btn {
    height: 30px;
    width: 20%;
    cursor: pointer;
    border: 1px solid black;
    border-radius: 3px;
    box-shadow: 2px 2px 2px black;
}

.Btn__Save {
    background-color: #c5c5c5;
}

.Input {
    padding: 10px;
}
.app {
    background: #f5f5f5;
    border: 1px solid black;
    border-radius: 3px;
    padding-left: 10px;
    box-sizing: content-box;
}

.Links {
    display: inline-block;
    width: 50%;
    border-right: 1px solid black;
    box-sizing: border-box;
}

.Form {
    display: inline-block;
    width: 50%;
    vertical-align: top;
    box-sizing: border-box;
    text-align: center;
}
